import { animate, keyframes, style, transition, trigger } from '@angular/animations';
import { Component, OnInit, ViewChild, Input, OnChanges, OnDestroy } from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { BehaviorSubject } from 'rxjs';

import { BaseDirective } from '../../../shared/globals/base.directive';
import { AppService } from '../../../services/app.service';
import { IBooking } from '../../../services/data/bookings.service';
import { Utils } from '../../../shared/utility.class';
import { IUser } from '../../../services/data/users.service';

import * as dayjs from 'dayjs';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import {
    MeetingDetailsOverlayComponent,
    MeetingDetailsModalData
} from '../../../overlays/meeting-details-overlay/meeting-details-overlay.component';
import { CONFIRM_METADATA } from '../../../overlays/confirm-modal/confirm-modal.component';

@Component({
    selector: 'schedule-event-list',
    templateUrl: './event-list.component.html',
    styleUrls: ['./event-list.component.scss'],
    animations: [
        trigger('show', [
            transition(
                'void => event',
                animate(
                    '.3s ease-out',
                    keyframes([
                        style({ height: 0, opacity: 0, offset: 0 }),
                        style({ height: '80px', opacity: 1, offset: 1 })
                    ])
                )
            )
        ])
    ]
})
export class ScheduleEventListComponent extends BaseDirective
    implements OnInit, OnChanges, OnDestroy {
    /** User to show events for */
    @Input() public user: IUser;
    @Input() public date = dayjs().valueOf();
    public model: any = {};
    public items: BehaviorSubject<IBooking[]> = new BehaviorSubject([]);
    public selected_index: number;

    public view_ref: MatDialogRef<any>;

    // @ViewChild('list') private list: ElementRef;

    @ViewChild(CdkVirtualScrollViewport, { static: true })
    private viewport: CdkVirtualScrollViewport;

    constructor(private service: AppService, private _dialog: MatDialog) {
        super();
    }

    public ngOnInit() {
        this.model.days = 15;
        this.checkScroll();
        this.items.subscribe(res => (this.model.list = res));
        this.subscription(
            'bookings',
            this.service.Bookings.listen('timeline', timeline => {
                let date = dayjs().startOf('d');
                const end = date.add(this.model.days, 'd');
                let list: IBooking[] = [];
                for (; date.isBefore(end, 'm'); date = date.add(1, 'd')) {
                    list = [...list, ...(timeline[date.format('YYYY/MM/DD')] || [])];
                }
                list = Utils.unique(list, 'order_id');
                if (this.user) {
                    list = list.filter(
                        i =>
                            i.organiser.email === this.user.email ||
                            !!i.attendees.find(j => j.email === this.user.email)
                    );
                }
                this.model.list = list;
                this.processBookings();
            })
        );
        this.loadBookings();
        this.interval('update', () => this.updateBookings(), 10 * 1000);
    }

    public ngOnChanges(changes: any) {
        if (changes.date) {
            this.timeout('date_change', () => this.changeDate(this.date), 500);
        }
        if (changes.user) {
            this.updateBookings();
            this.interval('update', () => this.updateBookings(), 10 * 1000);
        }
    }

    public ngOnDestroy(): void {
        super.ngOnDestroy();
        this.clearInterval('update');
        if (this.view_ref) {
            this.view_ref.close();
            this.view_ref = null;
        }
    }

    public loadBookings() {
        if (this.model.loading) {
            return;
        }
        return new Promise((resolve, reject) => {
            this.model.loading = true;
            const date = dayjs().startOf('d');
            const end = date.add(Math.max(3, this.model.days), 'd');
            const user = this.service.Users.current();
            if (!this.user) {
                this.user = this.service.Users.current();
            }
            const user_list =
                user.delegates && user.delegates.length > 0
                    ? [user.email, ...user.delegates]
                    : [user.email];
            this.service.Bookings.query({
                email: this.user
                    ? this.user.email
                    : user_list.reduce((a, v) => (a ? a + ',' : a) + v, ''),
                from: date.unix(),
                to: end.unix(),
                timezone_offset: new Date().getTimezoneOffset()
            }).then(
                items => {
                    this.service.Bookings.clear({ from: date.valueOf(), to: end.valueOf() });
                    this.service.Bookings.updateList(Utils.unique(items, 'icaluid'));
                    this.model.loading = false;
                    resolve();
                },
                _ => {
                    this.model.loading = false;
                    reject();
                }
            );
        });
    }

    public checkScroll() {
        this.timeout(
            'check_scroll',
            () => {
                if (!this.viewport) {
                    return this.timeout('atBottom', () => this.checkScroll());
                }
                const start = this.viewport.getRenderedRange().start;
                const end = this.viewport.getRenderedRange().end;
                const total = this.viewport.getDataLength();
                const from_start = dayjs()
                    .add(this.model.from_start, 'd')
                    .format('YYYY-MM-DD');
                const items = this.items.getValue();
                if (end === total) {
                    this.atBottom();
                } else if (
                    this.model.from_start > 0 &&
                    items.indexOf(items.find(i => i.id === from_start)) > start
                ) {
                    this.atTop();
                }
            },
            100
        );
    }

    public atBottom() {
        if (this.model.loading) {
            return;
        }
        this.model.days += 3;
        this.interval('update', () => this.updateBookings(), 5 * 1000);
        this.updateBookings();
    }

    public getViewLocation() {
        if (this.viewport) {
            const start = Math.floor(this.viewport.measureScrollOffset() / 80);
            const items = this.items.getValue();
            if (items && items.length > 0 && start < items.length) {
                const item = items[start];
                return item ? item.id : null;
            }
        }
        return null;
    }

    public atTop() {
        if (this.model.loading || this.model.from_start <= 0) {
            return;
        }
        const range = this.viewport.getRenderedRange();
        const now = dayjs().startOf('d');
        const items = this.items.getValue();
        const middle = Math.floor((range.start + range.end) / 2);
        const time = dayjs(items && items[middle] ? items[middle].date : undefined);
        this.model.from_start = Math.floor(time.diff(now, 'd'));
        this.updateBookings();
    }

    public scrollTo(id: string, offset: number = 0, smooth: boolean = true) {
        if (!id || (this.model.ignore && id !== this.model.ignore)) {
            return;
        }
        const items = this.items.getValue();
        const index = items.findIndex(i => i.id === id);
        if (index >= 0) {
            this.viewport.scrollToOffset(index * 80 + offset, smooth ? 'smooth' : 'auto');
        }
        this.timeout('ignore', () => (this.model.ignore = null), 1000);
    }

    public changeDate(date: number) {
        const now = dayjs().startOf('d');
        const day = dayjs(date);
        let time = day.subtract(3, 'd');
        if (time.isBefore(now, 'd')) {
            time = dayjs(now);
        }
        if (day.diff(now, 'd') < this.model.days) {
            this.model.ignore = day.format('YYYY-MM-DD');
            this.scrollTo(day.format('YYYY-MM-DD'));
        } else {
            this.model.from_start = time.diff(now, 'd');
            this.model.days = this.model.from_start + 8;
            this.updateBookings().then(() => {
                this.timeout('scroll', () => this.scrollTo(day.format('YYYY-MM-DD')), 1000);
            });
        }
    }

    public updateBookings() {
        return new Promise((resolve, reject) => {
            if (this.model.loading) {
                return reject();
            }
            if (!this.viewport) {
                return this.timeout('update_bookings', () =>
                    this.updateBookings().then(resolve, reject)
                );
            }
            if (!this.user) {
                this.user = this.service.Users.current();
            }
            const range = this.viewport.getRenderedRange();
            const now = dayjs().startOf('d');
            const items = this.items.getValue();
            const middle = Math.floor((range.start + range.end) / 2);
            const date = items && items[middle] ? items[middle].date : undefined;
            const start = dayjs(date)
                .subtract(3, 'd')
                .startOf('d');
            const end = dayjs(date)
                .add(3, 'd')
                .endOf('d');
            const from = start.isBefore(now, 'd') ? now.unix() : start.unix();
            if (dayjs(from).isAfter(end, 's')) {
                return;
            }
            this.setLoading(start.valueOf(), end.valueOf());
            const user = this.service.Users.current();
            const user_list =
                user.delegates && user.delegates.length > 0
                    ? [user.email, ...user.delegates]
                    : [user.email];
            this.model.loading = true;
            this.service.Bookings.query({
                email: this.user
                    ? this.user.email
                    : user_list.reduce((a, v) => (a ? a + ',' : a) + v, ''),
                from,
                to: end.unix(),
                timezone_offset: new Date().getTimezoneOffset()
            }).then(
                list => {
                    this.service.Bookings.clear({
                        from: start.isBefore(now, 'd') ? now.valueOf() : start.valueOf(),
                        to: end.valueOf()
                    });
                    this.service.Bookings.updateList(list);
                    this.model.loading = false;
                    resolve();
                },
                _ => {
                    this.model.loading = false;
                    reject();
                }
            );
        });
    }

    public setLoading(start: number, end: number) {
        let from = dayjs(start);
        const to = dayjs(end);
        const items: any[] = this.items.getValue();
        for (; from.isBefore(to, 'm'); from = from.add(1, 'd')) {
            const item = items.find(i => i.id === from.format('YYYY-MM-DD'));
            if (item) {
                item.loading = true;
            }
        }
    }

    public processBookings() {
        this.timeout(
            'process',
            () => {
                const offset = this.viewport.measureScrollOffset() % 80;
                const scroll_id = this.getViewLocation();
                this.model.list.sort((a, b) => a.date - b.date);
                let list: any[] = [];
                const now = dayjs();
                let date = dayjs().startOf('d');
                const end = date.add(this.model.days, 'd').endOf('d');
                for (; date.isBefore(end, 'd') || date.isSame(end, 'd'); date = date.add(1, 'd')) {
                    list.push({
                        order_id: date.format('YYYY-MM-DD'),
                        id: date.format('YYYY-MM-DD'),
                        type: 'date',
                        today: now.isSame(date, 'd'),
                        title: date.format('dddd, DD MMM'),
                        value: date.format('YYYY-MM-DD'),
                        date: date.valueOf()
                    });
                    const length = list.length;
                    for (const bkn of this.model.list) {
                        if (bkn.type && bkn.type !== 'event') {
                            continue;
                        }
                        const bkn_date = dayjs(bkn.for_date || bkn.date);
                        if (bkn_date.isSame(date, 'd')) {
                            const item = {
                                ...bkn,
                                room: bkn.room,
                                room_list: bkn.room_list,
                                status: bkn.status,
                                type: 'event'
                            };
                            list.push(item);
                        }
                    }
                    if (list.length === length) {
                        list.push({
                            order_id: `${date.format('YYYY-MM-DD')}-no-items`,
                            type: 'no-items',
                            title: 'No events',
                            date: date.valueOf()
                        });
                    }
                }
                list.forEach(bkn => {
                    const rooms = bkn.spaces || [];
                    bkn.is_declined = rooms.reduce(
                        (state, room) => state || bkn.approval_status[room] === 'declined',
                        false
                    );
                    bkn.is_tentative = rooms.reduce(
                        (state, room) =>
                            state ||
                            (bkn.approval_status[room] || '').indexOf('tentative') >= 0 ||
                            (bkn.approval_status[room] || '').indexOf('not') >= 0,
                        false
                    );
                });
                list.sort((a, b) => (a.for_date || a.date) - (b.for_date || b.date));
                list = Utils.unique(list, 'order_id');
                this.items.next(list);
                this.model.bookings = list.filter(i => i.id);
                if (this.model.ignore && !!list.find(i => i.id === this.model.ignore)) {
                    this.scrollTo(this.model.ignore);
                } else {
                    this.scrollTo(scroll_id, offset, false);
                }
            },
            50
        );
    }

    public view(item: any) {
        if (this.view_ref) {
            this.view_ref.close();
        }
        this.model.mainIndex = this.model.bookings.indexOf(item);
        const idx = this.model.mainIndex;
        this.view_ref = this._dialog.open<MeetingDetailsOverlayComponent, MeetingDetailsModalData>(
            MeetingDetailsOverlayComponent,
            {
                maxHeight: 'auto',
                maxWidth: 'auto',
                width: 'auto',
                height: 'auto',
                hasBackdrop: false,
                closeOnNavigation: true,
                data: {
                    booking: this.service.Bookings.item(item.id),
                    as_delegate:
                        this.user && this.user.email !== this.service.Users.current().email,
                    delegate: this.user.email,
                    is_first: idx === 0,
                    is_last: idx === this.model.bookings.length - 1
                }
            }
        );
        this.subscription(
            'details-modal',
            this.view_ref.componentInstance.event.subscribe(event => {
                if (event.reason === 'action') {
                    switch (event.metadata) {
                        case 'next':
                            this.nextBooking();
                            break;
                        case 'previous':
                            this.previousBooking();
                            break;
                    }
                }
            })
        );
        this.view_ref.afterClosed().subscribe(() => {
            this.unsub('details-modal');
            this.view_ref = null;
        });
    }

    public checkin() {
        const index = this.model.mainIndex;
        this.model.bookings[index].checkin = true;
    }

    public deletebooking() {
        this.model.bookings.splice(this.model.mainIndex, 1);
    }

    public nextBooking() {
        const index = this.model.mainIndex;
        const current_booking = this.model.bookings[index];
        const next_booking = this.model.bookings[index + 1];
        if (index < this.model.bookings.length - 1 && next_booking && next_booking.title) {
            this.view(next_booking);
        } else {
            this.view(current_booking);
        }
    }

    public previousBooking() {
        const index = this.model.mainIndex;
        const current_booking = this.model.bookings[index];
        const previous_booking = this.model.bookings[index - 1];
        if (index > 0 && previous_booking && previous_booking.title) {
            this.view(previous_booking);
        } else {
            this.view(current_booking);
        }
    }
}
